
import Scroll from '../../../base/scroll/scroll'
import Checkbtn from '../../../base/checkbtn/checkbtn'
import MButton from '../../../base/button/button'
import Vue from 'vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import md5 from 'vux/src/tools/md5/index.js'

import { mapGetters } from 'vuex'
export default {
name: "userMoney",
components: {
Scroll,
Checkbtn,
XInput,
Group,
MButton
},
computed: {
btnSaveState: {
get() {
let isSave = false
if (this.money >= 200 && this.money <= this.availableDeposit && this.pwd && this.msg && this.msg.length === 6) {
return true
}
return false
},
set(val) {
return val
}
},
availableDeposit: {
get() {
let deposit = 400
if (this.userCoin.availableDeposit) {
deposit = parseFloat(this.userCoin.availableDeposit).toFixed(2)
}
return deposit
},
set(val) {
return val
}
},
...mapGetters([
'userInfo',
'userCoin'
])
},
data() {
return {
btnCodeState: true,
msg: null,
pwd: null,
codeText: '',
time: 60,
timer: null,
chooseCheck: 1,
money: null
}
},
methods: {

getCode() {
if (this.btnCodeState) {
this.btnCodeState = false
this.$axios.post('/api/sms/send', {
telephone: this.userInfo.telephone,
type: 0
}).then((response) => {
if (response.data.code === '200') {
this.$vux.toast.text('', 'middle')
this.timer = setInterval(this.timeGo, 1000)
} else {
this.btnCodeState = true
this.$vux.alert.show({
title: '',
content: response.data.message,
})
}
}).catch((error) => {
this.btnCodeState = true
this.$vux.alert.show({
title: '',
content: '',
})
})
}
},

timeGo() {
if (this.time >= 0) {
this.codeText = `(${this.time}`
this.time--
} else {
this.codeText = ''
this.time = 60
clearInterval(this.timer)
this.timer = null
this.btnCodeState = true
}
},
getUserMoney() {
var _this = this
this.$vux.confirm.show({
title: '',
content: `<p>:${parseFloat(_this.money).toFixed(2)}</p><p>:${parseFloat(_this.money * 0.9).toFixed(2)}</p>`,
onConfirm() {
_this.doDeposit()
}
})
},
doDeposit() {
this.$axios.post('/api/withdrawApply/buyer/insertWithdrawApply', {
buyerUserId: this.userInfo.buyerUserId,
deposit: this.money,
telephone: this.userInfo.telephone
}).then((response) => {
const _this = this
if (response.data.code === '200') {
this.$vux.alert.show({
title: ',',
onHide() {
_this.$router.push({ name: 'user' })
}
})
} else {
this.$vux.alert.show({
title: '',
content: response.data.message,
})
}
}).catch((error) => {
this.$vux.alert.show({
title: '',
content: '',
})
})
}
}
}



